.carousel-teaser {
  @include divider-combo-2;
  background-color: $sunny-yellow;

  &__thumbnail {
    width: auto;
  }

  &__wrapper {
    height: 180px;
    display: table;
    width: 100%;
  }
  &__content {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
  }
}
.carousel-teaser-list {
  display: flex;
  flex-wrap: nowrap;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  padding: 0 5px;
  &__item {
    @include column($grid-twelve);
    flex: 0 0 auto;
    padding-top: 0;
    padding-bottom: 0;
  }
  &__item--container {
    width: 218px;
  }
}
